<template>
	<view class="out">
		<view class="item" v-for="item in goods" :key="item.id">
			<checkbox></checkbox>
			<text class="title">{{item.name}}</text>
			<text class="del" @click='remove(index)'>删除</text>
		</view>
	</view>
</template>

<script setup>
	import {ref} from 'vue'
	const goods = ref([
		{id:1,name:"小米"},
		{id:2,name:"oppo"},
		{id:3,name:"vivo"},
		{id:4,name:"苹果"},
	])
	const remove = (index)=>{
		goods.value.splice(index,1)
	}
</script>

<style lang="scss" scoped>
	.out {
		.item {
			.title{
				
			}
			.del {
				margin-left: 20px;
				color:red
			}
		}
	}
</style>
